
/* 주제/레벨 선택 전체 컨테이너 */
.explore-main-selector-box {
  position: absolute;
  left: 16px;
  right: 16px; /* 양쪽 16px 여백 보장 */
  top: 64px;
  width: auto;  /* 고정폭 제거로 작은 화면에서 넘침 방지 */
  max-width: calc(100% - 80px);
  min-height: 0;
  background: #FFFFFF;
  box-shadow: 0px 0px 8px rgba(10, 26, 51, 0.18);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 16px 16px 56px 16px;
  gap: 10px;
  z-index: 100;
  transition: height .2s;
  box-sizing: border-box;
}

/* 내부 컨텐츠 오토레이아웃 */
.explore-main-selector-content {
  display: flex;
  flex-direction: column;
  align-items: stretch; /* 자식이 가로 100% 사용 */
  padding: 0;
  padding-bottom: 16px;
  gap: 16px;
  width: 100%;
  min-height: 100px;
}

/* 세부주제 드롭다운+확인버튼 컨테이너 */
.explore-main-subtopic-dropdown-container {
  display: flex;
  flex-direction: column;
  gap: 16px;   /* 드롭다운 ↔ 버튼 16px */
  width: 100%; /* 부모(.explore-main-selector-box) 패딩 16px과 맞춰 풀폭 사용 */
  padding-bottom: 10px; /* 버튼 ↔ 컨테이너 끝 16px */
}

/* 드롭다운 헤더 (old duplicate removed; using improved block below) */

/* 확인 버튼 */
.explore-main-selector-confirm-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 11px 0;
  width: 100%;
  height: 40px;
  background: linear-gradient(104.45deg, #448FFF -6.51%, #4833D0 105.13%);
  border-radius: 8px;
  border: none;
  cursor: pointer;
  margin: 0;
  position: relative;
  z-index: 101;
}

.explore-main-selector-confirm-btn-text {
  font-family: 'Roboto';
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: #fff;
}

/* 중복된 초기 규격(294px 고정폭) 제거 — 아래 섹션별 블록과 100% 플루이드 규격 사용 */

/* 난이도/주제 버튼 */


.explore-main-level-btn.selected,
.explore-main-topic-btn.selected {
  background: #F4F6FA;
}

.explore-main-level-btn-text,
.explore-main-topic-btn-text {
  font-family: 'Roboto';
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.04em;
  color: #234E8F;
}

.explore-main-level-btn-text.selected,
.explore-main-topic-btn-text.selected {
  font-weight: 700;
  background: linear-gradient(104.45deg, #448FFF -6.51%, #4833D0 105.13%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
/* TopicLevelSelector Overlay */
.explore-main-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 50;
}

/* Duplicate fixed-size selector styles removed; using canonical block above with 16px gutters */

/* Level Section */
.explore-main-level-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
  gap: 12px;
  width: 100%;
  height: 63px;
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
}
.explore-main-level-title {
  width: 100%;
  height: 19px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.02em;
  color: #1B1B1B;
  flex: none;
  order: 0;
  align-self: stretch;
  /* non-standard text-fill-color removed */
}
.explore-main-level-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  width: 105.6%;
  height: 32px;
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
  box-sizing: border-box; /* 내부 여백 포함하여 계산 */
  padding-right: 16px;    /* 카드 내부 오른쪽 16px 여백 */
}
.explore-main-level-btn {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 10px;
  gap: 10px;
  width: 100%;
  height: 32px;
  background: #E5EFFF;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  flex: 1 1 0; /* 3개 버튼이 균등 분배 */
  min-width: 0; /* 긴 텍스트에서도 줄맞춤 */
}
.explore-main-level-btn.selected {
  background:
    linear-gradient(#F4F6FA, #F4F6FA) padding-box,
    linear-gradient(104.45deg, #448FFF -6.51%, #4833D0 105.13%) border-box;
  border: 1px solid transparent;
  border-radius: 8px; /* 카드와 동일하게 */
  box-sizing: border-box;
}
.explore-main-level-btn-text {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.04em;
  color: #234E8F;
}
.explore-main-level-btn-text.selected {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.04em;
  background: linear-gradient(104.45deg, #448FFF -6.51%, #4833D0 105.13%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

/* Topic Section */
.explore-main-topic-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
  gap: 12px;
  width: 100%;
  height: 63px;
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
}
.explore-main-topic-title {
  width: 100%;
  height: 19px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.02em;
  color: #1B1B1B;
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
}
.explore-main-topic-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  width: 100%;
  height: 32px;
}
.explore-main-topic-row.dynamic {
  height: auto;
  min-height: 32px;
  max-height: 320px;
  overflow-y: auto;
  transition: height 0.3s;
}
.explore-main-topic-btn {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 10px;
  gap: 10px;
  width: 62px;
  height: 32px;
  background: #E5EFFF;
  border-radius: 8px;
  border: none;
  cursor: pointer;
}
.explore-main-topic-btn.selected {
  background: #F4F6FA;
  border: 2px solid;
  border-image: linear-gradient(104.45deg, #448FFF -6.51%, #4833D0 105.13%) 1;
  border-radius: 32px;
  box-sizing: border-box;
}
.explore-main-topic-btn-text {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.04em;
  color: #234E8F;
}

/* Subtopic Section */
.explore-main-subtopic-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
  gap: 0;
  width: 100%;
  height: 86px; /* 콘텐츠 높이에 맞춰 확장 */
  flex: none;
  order: 2;
  align-self: stretch;
  flex-grow: 0;
  margin-bottom: 0;
}
.explore-main-subtopic-title {
  width: 100%;
  height: 19px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.02em;
  color: #1B1B1B;
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
  margin-bottom: 16px;
}

/* Subtopic Dropdown */

/* 세부 주제 드롭다운 영역 개선 */
.explore-main-subtopic-dropdown {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
  min-width: 0; /* 컨테이너 폭에 맞춰 유연하게 */
  max-width: 100%;
  background: #fff;
  border: 1px solid #9B9B9B;
  border-radius: 12px;
  transition: box-shadow 0.2s, height 0.3s;
  z-index: 100;
  height: 44px;
  overflow: visible;
  margin-top: 0;
}
.explore-main-subtopic-dropdown.open {
  z-index: 100;
  box-shadow: 0 4px 24px rgba(10,26,51,0.18);
  height: auto !important;
}
.explore-main-subtopic-dropdown-header {
  width: 100%;
  padding: 12px 16px;
  background: none;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #282828;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: background 0.15s;
}
.explore-main-subtopic-dropdown-header.open {
  border: 1px solid #9B9B9B;
  border-radius: 12px 12px 0 0;
}
.explore-main-subtopic-dropdown-text {
  flex: 1;
  text-align: left;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 14px;
  color: #4D4D4D;
}
.explore-main-subtopic-dropdown-arrow {
  margin-left: 8px;
  width: 16px;
  height: 16px;
  transition: transform 0.2s;
}
.explore-main-subtopic-dropdown-arrow.open {
  transform: rotate(180deg);
}
.explore-main-subtopic-dropdown-option {
  width: 100%;
  padding: 12px 16px;
  background: none;
  border: none;
  border-bottom: 1px solid #F5F5F5;
  font-size: 16px;
  color: #474747;
  text-align: left;
  cursor: pointer;
  transition: background 0.15s;
  border-radius: 0;
}
.explore-main-subtopic-dropdown-option:hover {
  background: #F5F7FB;
}
.explore-main-subtopic-dropdown-option.last {
  border-bottom: none;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.explore-main-subtopic-dropdown-option-text {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 14px;
  color: #4D4D4D;
  flex: 1;
  text-align: left;
}

/* Confirm Button */
.explore-main-selector-confirm-btn-text {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: #fff;
  flex: none;
  order: 0;
  flex-grow: 0;
}
/* 퀴즈 풀러가기 CTA 버튼 (FloatingQuizCTA.js) */
.floating-quiz-cta-wrap {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 100%;
  z-index: 120;
  pointer-events: none;
  padding: 0 16px;
  box-sizing: border-box;
}
.floating-quiz-cta-btn {
  pointer-events: auto;
  width: 100%;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: -0.02em;
  cursor: pointer;
  transition: background 0.15s;
}
.floating-quiz-cta-btn-gradient {
  background: linear-gradient(104.45deg,#448FFF -6.51%, #4833D0 105.13%);
  color: #FFFFFF;
  border: none;
}
.floating-quiz-cta-btn-outline {
  background: #FFFFFF;
  color: #4D4D4D;
  border: 1px solid #ddd;
}
.floating-quiz-cta-btn-shadow {
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
}
/* 출석 카드 */
.explore-main-attendance-card {
  position: relative; /* 추가 */
  z-index: 10;   
  width: calc(100% - 32px);
  max-width: 100%;
  margin: 24px auto 0 auto;
  background: #FFFFFF;
  box-shadow: 0 0 2px rgba(0,0,0,0.25);
  border-radius: 16px;
  padding: 12px 0;
  box-sizing: border-box;
}
.explore-main-attendance-dates {
  display: flex;
  justify-content: space-around;
}
.explore-main-attendance-date {
  width: 18px;
  text-align: center;
  font-size: 10px;
  font-weight: 500;
  color: #B2B2B2;
}
.explore-main-attendance-date.today {
  color: #448FFF;
}
.explore-main-attendance-icons {
  display: flex;
  justify-content: space-around;
  margin-top: 8px;
}

/* 진행도 바 */
.explore-main-progress-bar {
  position: relative; /* 추가 */
  z-index: 10;   
  width: calc(100% - 32px);
  max-width: 100%;
  margin: 24px auto 0 auto;
  height: 12px;
  background: #DDEBFF;
  border-radius: 50px;
  overflow: hidden;
  box-sizing: border-box;
}
.explore-main-progress-bar-inner {
  height: 100%;
  background: linear-gradient(104.45deg,#448FFF -6.51%,#4833D0 105.13%);
  border-radius: 50px;
  transition: width .3s;
}

/* 진행도 바 양쪽 숫자 */
.explore-main-progress-numbers {
  position: relative; /* 추가 */
  z-index: 10;   
  width: calc(100% - 32px);
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: -22px;
}
.explore-main-progress-number {
  width: 32px;
  height: 32px;
  background: #DDEAFF;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  color: #ADC5EB;
  letter-spacing: -0.02em;
  box-shadow: none;
}
.explore-main-progress-number.active {
  background: #448FFF;
  color: #FFFFFF;
  box-shadow: 0 0 2px #448FFF;
}

/* 징검다리 영역 래퍼 */
.explore-main-steppingstones-wrap {
  z-index: 1; 
  width: calc(100% - 32px);
  min-height: 100%;
  margin: 0;

  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto; /* 추가 */
}
.stepping-stone-item {
  position: relative; /* z-index 적용 위해 */
  z-index: 1; /* 달력/진행도바보다 뒤로 */
}
/* 하단 고정 CTA 버튼 */
.explore-main-cta-fixed {
  position: fixed;
  left: 16px;
  right: 16px;
  bottom: 88px;
  width: calc(100vw - 32px);
