/* ==============================================
   FIX: 단어장/오답노트 드롭다운 높이 오토 정렬
   - 기존 상단부의 고정 height, grid 규칙과 충돌해
     드롭다운 높이가 어색하게 보이는 문제를 해소합니다.
   - 가장 마지막에 둔 강한 오버라이드로 일관성 보장.
   ============================================== */
.explore-main-menu-popup {
  /* 외곽 높이는 고정, 내부만 정렬 */
  height: 76px !important; /* Figma 고정 높이 */
  padding: 16px 12px !important; /* 상하 16px 패딩 유지 */
  box-sizing: border-box;
